.display-card-container{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    width: 100%;
    margin-bottom: 10px;
}
.display-card-container .display-card img{
    width: 100%;
    height: 400px;
    background: #e1e1e1;
    -webkit-animation: loading-card 1.5s 10;
            animation: loading-card 1.5s 10;
}
@media only screen and (min-width: 580px) {
    .display-card-container{
        width: 220px;
    }
    .display-card-container .display-card img{
        width: 220px;
        height: 300px;
    }
}

@-webkit-keyframes loading-card{
    0%{
        background: #e1e1e1;
    }
    50%{
        background: #fff;
    }
    100%{
        background: #e1e1e1;
    }
}

@keyframes loading-card{
    0%{
        background: #e1e1e1;
    }
    50%{
        background: #fff;
    }
    100%{
        background: #e1e1e1;
    }
}



