.loading-card-container{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    width: 100%;
    margin-bottom: 20px;
}
.loading-card-container .loading-card{
    width: 100%;
    height: 300px;
    background: #e1e1e1;
    -webkit-animation: loading 1.5s 10;
            animation: loading 1.5s 10;
}
.loading-card-container .loading-title{
    width: 70%;
    height: 20px;
    background: #e1e1e1;
    -webkit-animation: loading 1.5s 10;
            animation: loading 1.5s 10;
    margin-top: 10px;
}
@media only screen and (min-width: 580px) {
    .loading-card-container{
        width: 220px;
    }
    .loading-card-container .display-card{
        width: 220px;
    }
}

@-webkit-keyframes loading{
    0%{
        opacity: 1
    }
    50%{
        opacity: 0.2
    }
    100%{
        opacity: 1
    }
}

@keyframes loading{
    0%{
        opacity: 1
    }
    50%{
        opacity: 0.2
    }
    100%{
        opacity: 1
    }
}



